import React from 'react';
import './Websites.css';



import WebsitesNav from '../../Components/Websites/WebsitesNav/WebsitesNav.js';
import WebsitesAbout from '../../Components/Websites/WebsitesAbout/WebsitesAbout.js';
import WebsitesList from '../../Components/Websites/WebsitesList/WebsitesList.js';

class Websites extends React.Component {
  constructor(){
      super()
      this.state={
        route: 'home',
      }
  }

  componentDidMount(){
  }

  onRouteChange = (route) => {
    this.setState({route : route});
  }
  onMainRouteChange = (MainRoute) => {
    this.props.MainRouteChange(MainRoute);
  }

  render(){
    return (
    <div className="Websites">
      
      <WebsitesNav 
        onRouteChange={this.onRouteChange}
        onMainRouteChange={this.onMainRouteChange}
      />
      <WebsitesAbout/>
      <WebsitesList/>
    </div>
  );
    }
  
}

export default Websites;
