import React from 'react';
import './Apps.css';



import Nav from '../../Components/Apps/Nav/Nav.js';
import AppsAbout from '../../Components/Apps/AppsAbout/AppsAbout.js';
import AppsList from '../../Components/Apps/AppsList/AppsList.js';

import SuperCalculator from '../../Components/Apps/AppsList/SuperCalculator/SuperCalculator.js';

class Apps extends React.Component {
  constructor(){
      super()
      this.state={
        route: 'home',
      }
  }

  componentDidMount(){
  }

  onRouteChange = (route) => {
    this.setState({route : route});
  }
  onMainRouteChange = (MainRoute) => {
    this.props.MainRouteChange(MainRoute);
  }

  render(){
    return (
    <div className="Apps">
      
      <Nav 
        onRouteChange={this.onRouteChange}
        onMainRouteChange={this.onMainRouteChange}
      />
      {this.state.route === 'home'
        ? <div className='homeplus'>
            <AppsAbout/>
            <AppsList
              onRouteChange={this.onRouteChange}
            />
          </div>
        : (this.state.route === 'SuperCalculator'
          ? <SuperCalculator/>
          : <AppsList
              onRouteChange={this.onRouteChange}
          />
          )
      }   
      
    </div>
  );
    }
  
}

export default Apps;
