import './WebsitesList.css';
import GamingPortal from './GamingPortal.PNG';
import CroxiShop from './CroxiShop.PNG';




const WebsitesList = () => {
		return(
			<div id='WebsitesList' className='WebsitesList'>
				
				<div className='WebsitesList-Container'>
					<div className='SingleWebsite'>
						<img alt='SmartBrain SingleWebsite' src={GamingPortal}></img> 
						<div className='SingleWebsite-Text'> 
							<a onClick={() => alert("Website will be available soon!")} href='#WebsitesList'> <span> TBD  </span> </a>
						</div>
					</div>
					<div className='SingleWebsite'>
						<img alt='SmartBrain SingleWebsite' src={CroxiShop}></img> 
						<div className='SingleWebsite-Text'> 
							<a onClick={() => alert("Website will be available soon!")} href='#WebsitesList'> <span> TBD </span> </a>
						</div>
					</div>
				</div>
			</div>
			)
}


export default WebsitesList