import React from 'react';
import './App.css';

import MouseParticles from 'react-mouse-particles';

import LandingPage from './LandingPage/LandingPage.js';
import Apps from './Apps/Apps.js';
import Games from './Games/Games.js';
import Websites from './Websites/Websites.js';

class App extends React.Component {
  constructor(){
      super()
      this.state={
        mainRoute: 'Landing',
      }
  }

  MainRouteChange = (MainRoute) => {
    this.setState({mainRoute : MainRoute});
  }
  render(){
    return (
      <div className="App">
        <MouseParticles alpha={.1} g={5} num={1} color={["#2FD0B8", "#2F97D0", "#2FD068"]} cull="col,image-wrapper" life={1.5} v={0.5}/>
        {this.state.mainRoute === 'Landing'
          ? <LandingPage MainRouteChange={this.MainRouteChange}/>
          : (this.state.mainRoute === 'Apps'
            ? <Apps MainRouteChange={this.MainRouteChange}/>
            : (this.state.mainRoute === 'Games'
              ? <Games MainRouteChange={this.MainRouteChange}/>
              : (this.state.mainRoute === 'Websites'
                ? <Websites MainRouteChange={this.MainRouteChange}/>
                : <LandingPage MainRouteChange={this.MainRouteChange}/>
                )
              )
            )
        }   
      </div>
    );
  }
  
}

export default App;