import React from 'react';
import './GamesNav.css';
import { SiHomeassistant, SiGitextensions, SiApplearcade, SiWebmoney, SiAppannie} from "react-icons/si";

import Logo from './Logo.png';
/*
Icons Used from React-icons (https://react-icons.github.io/react-icons/) :
1. SiHomeassistant for Home
2. SiPagekit for CV
3. SiProgress for Projects
4. SiAppannie for Apps
5. SiApplearcade for Games
6. SiWebmoney for Websites
7. SiAzuredataexplorer for Contact
*/
const GamesNav = ({onRouteChange, onMainRouteChange}) => {

		return(
			<div className='GamesNavigation'>
				<div className='GamesNav'>
					<div className='GamesNav-Profolio'>
						<li  onClick={() => onMainRouteChange('Landing')} className='GamesNav-Profolio-item'> <a href='#Landing'><span className='GamesNav-text'>Profolio</span> <SiGitextensions className='GamesNav-icon'/> </a></li>
					</div>
					<div className='GamesNav-list'>

						<ul className='GamesNavList'>
							<li onClick={() => onRouteChange('home')} className='GamesNav-item'> <a href='#Landing'><span className='GamesNav-text'>Home</span> <SiHomeassistant className='GamesNav-icon'/> </a></li>
							<li onClick={() => onMainRouteChange('Apps')} className='GamesNav-item'> <a href='#Apps'><span className='GamesNav-text'>Apps</span> <SiAppannie className='GamesNav-icon'/> </a></li>
							<li onClick={() => onMainRouteChange('Websites')} className='GamesNav-item'> <a href='#Websites'><span className='GamesNav-text'>Websites</span> <SiWebmoney className='GamesNav-icon'/> </a></li>
							
						</ul>
				</div>
				<div className='GamesNav-Logo'>
					<img className='Logo' src={Logo} alt='Sahels profile'></img>
					<a className='Logo-name' href=''> Sahel Wahdain </a>

				</div>
				
			</div>
			<div className='GamesNav-Floor'>
				<div className='GamesNav-Floor-Shadow'>
					
				</div>
					
			</div>
		</div>
			
	);
}


export default GamesNav