import React from 'react';
import './WebsitesNav.css';
import { SiHomeassistant, SiGitextensions, SiApplearcade, SiWebmoney, SiAppannie} from "react-icons/si";

import Logo from './Logo.png';
/*
Icons Used from React-icons (https://react-icons.github.io/react-icons/) :
1. SiHomeassistant for Home
2. SiPagekit for CV
3. SiProgress for Projects
4. SiAppannie for Apps
5. SiApplearcade for Websites
6. SiWebmoney for Websites
7. SiAzuredataexplorer for Contact
*/
const WebsitesNav = ({onRouteChange, onMainRouteChange}) => {

		return(
			<div className='WebsitesNavigation'>
				<div className='WebsitesNav'>
					<div className='WebsitesNav-Profolio'>
						<li  onClick={() => onMainRouteChange('Landing')} className='WebsitesNav-Profolio-item'> <a href='#Landing'><span className='WebsitesNav-text'>Profolio</span> <SiGitextensions className='WebsitesNav-icon'/> </a></li>
					</div>
					<div className='WebsitesNav-list'>

						<ul className='WebsitesNavList'>
							<li onClick={() => onRouteChange('home')} className='WebsitesNav-item'> <a href='#Landing'><span className='WebsitesNav-text'>Home</span> <SiHomeassistant className='WebsitesNav-icon'/> </a></li>
							<li onClick={() => onMainRouteChange('Apps')} className='WebsitesNav-item'> <a href='#Apps'><span className='WebsitesNav-text'>Apps</span> <SiAppannie className='WebsitesNav-icon'/> </a></li>
							<li onClick={() => onMainRouteChange('Games')} className='SideNav-item'> <a href='#Games'><span className='SideNav-text'>Games</span> <SiApplearcade className='SideNav-icon'/> </a></li>
							
						</ul>
				</div>
				<div className='WebsitesNav-Logo'>
					<img className='Logo' src={Logo} alt='Sahels profile'></img>
					<a className='Logo-name' href=''> Sahel Wahdain </a>

				</div>
				
			</div>
			<div className='WebsitesNav-Floor'>
				<div className='WebsitesNav-Floor-Shadow'>
					
				</div>
					
			</div>
		</div>
			
	);
}


export default WebsitesNav