import './Projects.css';
import CroxtonApps from './CroxtonApps.PNG';
import CroxtonGames from './CroxtonGames.PNG';
import CroxtonWebsites from './CroxtonWebsites.PNG';





const Projects = ({onMainRouteChange}) => {
		return(
			<div id='Projects' className='Projects'>
				<div className='Projects-Title'>
					<span className='SingleWord'>
						<span className='Letter One' content="P">P</span>
						<span className='Letter Three' content="r">r</span>
						<span className='Letter Two' content="o">o</span>
						<span className='Letter Three' content="j">j</span>
						<span className='Letter Two' content="e">e</span>
						<span className='Letter Two' content="c">c</span>
						<span className='Letter Three' content="t">t</span>
						<span className='Letter Four' content="s">s</span>
					</span>
				</div>
				<div className='Projects-Container'>
					<div className='Project'>
						<img alt='CroxtonApps project' src={CroxtonApps}></img> 
						<div className='Project-Text'> 
							<span onClick={() => onMainRouteChange('Apps')}> Visit  </span>
						</div>
					</div>
					<div className='Project'>
						<img alt='CroxtonGames project' src={CroxtonGames}></img> 
						<div className='Project-Text'> 
							<span onClick={() => onMainRouteChange('Games')}> Visit  </span>
						</div>
					</div>
					<div className='Project'>
						<img alt='CroxtonWebsites project' src={CroxtonWebsites}></img> 
						<div className='Project-Text'> 
							<span onClick={() => onMainRouteChange('Websites')}> Visit  </span>
						</div>
					</div>
					
				</div>
			</div>
			)
}


export default Projects