import React from 'react';
import './Games.css';



import GamesNav from '../../Components/Games/GamesNav/GamesNav.js';
import GamesAbout from '../../Components/Games/GamesAbout/GamesAbout.js';
import GamesList from '../../Components/Games/GamesList/GamesList.js';

class Games extends React.Component {
  constructor(){
      super()
      this.state={
        route: 'home',
      }
  }

  componentDidMount(){
  }

  onRouteChange = (route) => {
    this.setState({route : route});
  }
  onMainRouteChange = (MainRoute) => {
    this.props.MainRouteChange(MainRoute);
  }

  render(){
    return (
    <div className="Games">
    
      <GamesNav 
        onRouteChange={this.onRouteChange}
        onMainRouteChange={this.onMainRouteChange}
      />
      <GamesAbout/>
      <GamesList/>
    </div>
  );
    }
  
}

export default Games;
