import React from 'react';
import './SideNav.css';
import Logo from './Logo.png';
import { SiHomeassistant, SiPagekit, SiProgress, SiAppannie, SiApplearcade, SiWebmoney, SiAzuredataexplorer, SiWhatsapp, SiGmail, SiLinkedin } from "react-icons/si";

/*
Icons Used from React-icons (https://react-icons.github.io/react-icons/) :
1. SiHomeassistant for Home
2. SiPagekit for CV
3. SiProgress for Projects
4. SiAppannie for Apps
5. SiApplearcade for Games
6. SiWebmoney for Websites
7. SiAzuredataexplorer for Contact
*/
const SideNav = ({onRouteChange, onMainRouteChange}) => {

		return(
			<div className='SideNav'>
				
				<div className='SideNav-Logo'>
					<img className='Logo' src={Logo} alt='Sahels profile'></img>
					<a className='Logo-name' href=''> Sahel Wahdain </a>
					<span><hr/></span> 
				</div>
				
				<div className='SideNav-list'>
					
					<ul className='SideNav-Unorderedlist'>
						<li onClick={() => onRouteChange('home')}  className='SideNav-item'> <a href='#Landing'><span className='SideNav-text'>Home</span> <SiHomeassistant className='SideNav-icon'/> </a></li>
						<li onClick={() => onRouteChange('about')}  className='SideNav-item'> <a href='#Profile'><span className='SideNav-text'>About</span> <SiPagekit className='SideNav-icon'/> </a></li>
						<li onClick={() => onRouteChange('projects')}  className='SideNav-item'> <a href='#Projects'><span className='SideNav-text'>Projects</span> <SiProgress className='SideNav-icon'/> </a></li>
						<li onClick={() => onMainRouteChange('Apps')} className='SideNav-item'> <a href='#Apps'><span className='SideNav-text'>Apps</span> <SiAppannie className='SideNav-icon'/> </a></li>
						<li onClick={() => onMainRouteChange('Games')} className='SideNav-item'> <a href='#Games'><span className='SideNav-text'>Games</span> <SiApplearcade className='SideNav-icon'/> </a></li>
						<li onClick={() => onMainRouteChange('Websites')} className='SideNav-item'> <a href='#Websites'><span className='SideNav-text'>Websites</span> <SiWebmoney className='SideNav-icon'/> </a></li>
						<li onClick={() => onRouteChange('contactInfo')}  className='SideNav-item'> <a href='#Contact'><span className='SideNav-text'>Contact</span> <SiAzuredataexplorer className='SideNav-icon'/></a> </li>
					</ul>
					
				</div>
				
					
						<div className='SideNav-Contact-Information'>
							<span><hr/></span> 
							<div className='SideNav-Contact-Method SideNav-Contact-LinkedIn'>
								<a href='https://www.linkedin.com/in/sahel-wahdain/' target='_blank'><SiLinkedin className='SideNav-Contact-Icon'/> <span className='SideNav-Contact-text'>LinkedIn </span></a>
							</div>
							<div className='SideNav-Contact-Method SideNav-Contact-Email'>
								<a href='mailto:sahelwahdain@gmail.com'><SiGmail className='SideNav-Contact-Icon'/> <span className='SideNav-Contact-text'>Email </span></a>
							</div>
							<div className='SideNav-Contact-Method SideNav-Contact-Phone'>
								<a href='https://wa.me/00601137121184' target='_blank'><SiWhatsapp className='SideNav-Contact-Icon'/> <span className='SideNav-Contact-text'>What'sApp </span></a>
							</div>
						</div>
					
				
			</div>
			);
}


export default SideNav