import React, { useState } from 'react';
import {SiPagekit} from "react-icons/si";
import './AppsAbout.css';

/*
Icons Used from React-icons (https://react-icons.github.io/react-icons/) :
1. SiHomeassistant for Home
2. SiPagekit for CV
3. SiProgress for Projects
4. SiAppannie for Apps
5. SiApplearcade for Games
6. SiWebmoney for Websites
7. SiAzuredataexplorer for Contact
*/
const AppsAbout = ({onRouteChange, onMainRouteChange}) => {

	const [AboutActive, setAboutActive] = useState("false");
		const AboutToggle = () => {
			setAboutActive(!AboutActive);
		};

		return(
			<div 
				className={`AppsAbout ${AboutActive ? "AppsAbout-Hidden" : "AppsAbout-NotHidden"}`}
				onClick={AboutToggle}
			>
				<div className={`AppsAbout-Website  ${AboutActive ? "Hidden-Text" : "NotHidden-Text"}`}>
					<h2> About the Website</h2>
					<p> This website contains the Apps that I developed. The Apps are listed below, feel free to explore them!</p>
				</div>
				<div className={`AppsAbout-Button  ${AboutActive ? "NotHidden-Text" : "Hidden-Text"}`}
				>
					<span> About </span>
				</div>
				<div className={`AppsAbout-App  ${AboutActive ? "Hidden-Text" : "NotHidden-Text"}`}>
					<h2> About the Apps</h2>
					<p> Each app is developed from scratch using React (HTML/CSS/JS). Every App serves as a component that can be used in other websites. Contact me for App requests!</p>
				</div>
			</div>
			
	);
}


export default AppsAbout