import React from 'react';
import './Nav.css';
import { SiHomeassistant, SiGitextensions, SiApplearcade, SiWebmoney} from "react-icons/si";

import Logo from './Logo.png';
/*
Icons Used from React-icons (https://react-icons.github.io/react-icons/) :
1. SiHomeassistant for Home
2. SiPagekit for CV
3. SiProgress for Projects
4. SiAppannie for Apps
5. SiApplearcade for Games
6. SiWebmoney for Websites
7. SiAzuredataexplorer for Contact
*/
const Nav = ({onRouteChange, onMainRouteChange}) => {

		return(
			<div className='Navigation'>
				<div className='Nav'>
					<div className='Nav-Profolio'>
						<li  onClick={() => onMainRouteChange('Landing')} className='Nav-Profolio-item'> <a href='#Landing'><span className='Nav-text'>Profolio</span> <SiGitextensions className='Nav-icon'/> </a></li>
					</div>
					<div className='Nav-list'>

						<ul className='NavList'>
							<li onClick={() => onRouteChange('home')} className='Nav-item'> <a href='#Landing'><span className='Nav-text'>Home</span> <SiHomeassistant className='Nav-icon'/> </a></li>
							<li onClick={() => onMainRouteChange('Games')} className='SideNav-item'> <a href='#Games'><span className='SideNav-text'>Games</span> <SiApplearcade className='SideNav-icon'/> </a></li>
							<li onClick={() => onMainRouteChange('Websites')} className='SideNav-item'> <a href='#Websites'><span className='SideNav-text'>Websites</span> <SiWebmoney className='SideNav-icon'/> </a></li>
							
						</ul>
				</div>
				<div className='Nav-Logo'>
					<img className='Logo' src={Logo} alt='Sahels profile'></img>
					<a className='Logo-name' href=''> Sahel Wahdain </a>

				</div>
				
			</div>
			<div className='Nav-Floor'>
				<div className='Nav-Floor-Shadow'>
					
				</div>
					
			</div>
		</div>
			
	);
}


export default Nav