import './GamesList.css';
import MemoryGame from './MemoryGame.PNG';
import FallingTiles from './FallingTiles.PNG';




const GamesList = () => {
		return(
			<div id='GamesList' className='GamesList'>
				
				<div className='GamesList-Container'>
					<div className='SingleGame'>
						<img alt='SingleGame' src={MemoryGame}></img> 
						<div className='SingleGame-Text'> 
							<a onClick={() => alert("Game will be available soon!")} href='#GamesList'> <span> TBD </span> </a>
						</div>
					</div>
					<div className='SingleGame'>
						<img alt='SmartBrain SingleGame' src={FallingTiles}></img> 
						<div className='SingleGame-Text'> 
							<a onClick={() => alert("Game will be available soon!")} href='#GamesList'> <span> TBD </span> </a>
						</div>
					</div>
				</div>
			</div>
			)
}


export default GamesList