import React, { useState } from 'react';
import {SiPagekit} from "react-icons/si";
import './GamesAbout.css';

/*
Icons Used from React-icons (https://react-icons.github.io/react-icons/) :
1. SiHomeassistant for Home
2. SiPagekit for CV
3. SiProgress for Projects
4. SiGameannie for Games
5. SiGamelearcade for Games
6. SiWebmoney for Websites
7. SiAzuredataexplorer for Contact
*/
const GamesAbout = ({onRouteChange, onMainRouteChange}) => {

	const [AboutActive, setAboutActive] = useState("false");
		const AboutToggle = () => {
			setAboutActive(!AboutActive);
		};

		return(
			<div 
				className={`GamesAbout ${AboutActive ? "GamesAbout-Hidden" : "GamesAbout-NotHidden"}`}
				onClick={AboutToggle}
			>
				<div className={`GamesAbout-Website  ${AboutActive ? "Hidden-Text" : "NotHidden-Text"}`}>
					<h2> About the Website</h2>
					<p> This website contains the Games that I developed. The Games are listed below, feel free to explore them!</p>
				</div>
				<div className={`GamesAbout-Button  ${AboutActive ? "NotHidden-Text" : "Hidden-Text"}`}
				>
					<span> About </span>
				</div>
				<div className={`GamesAbout-Game  ${AboutActive ? "Hidden-Text" : "NotHidden-Text"}`}>
					<h2> About the Games</h2>
					<p> Each Game is developed from scratch using React (HTML/CSS/JS). Every Game serves as a component that can be used in other websites. Contact me for Game requests!</p>
				</div>
			</div>
			
	);
}


export default GamesAbout