import React from 'react';
import './SuperCalculator.css';

import BasicCalculator from './BasicCalculator/BasicCalculator.js';


class SuperCalculator extends React.Component {
  constructor(){
      super()
      this.state={
        route: 'home',
      }
  }

  componentDidMount(){
  }

  onRouteChange = (route) => {
    this.setState({route : route});
  }
  onMainRouteChange = (MainRoute) => {
    this.props.MainRouteChange(MainRoute);
  }

  render(){
    return (
    <div className="SuperCalculator">
      <BasicCalculator/>
      
    </div>
  );
    }
  
}

export default SuperCalculator;
