import './AppsList.css';
import SuperCalculator from './SuperCalculator/SuperCalculator.PNG';
import WeatherApp from './WeatherApp.PNG';
import RoboFriends from './RoboFriends.PNG';
import SmartBrain from './SmartBrain.PNG';




const AppsList = ({onRouteChange}) => {
		return(
			<div id='AppsList' className='AppsList'>
				
				<div className='AppsList-Container'>
					<div className='SingleApp'>
						<img alt='SmartBrain SingleApp' src={RoboFriends}></img> 
						<div className='SingleApp-Text'> 
							<a href='https://croxtonhale.github.io/RoboFriends/' target='_blank'> <span> Visit  </span> </a>
						</div>
					</div>
					<div className='SingleApp'>
						<img alt='SmartBrain SingleApp' src={SmartBrain}></img> 
						<div className='SingleApp-Text'> 
							<a href='https://croxtonhale.github.io/SmartBrain/' target='_blank'> <span> Visit  </span> </a>
						</div>
					</div>
					<div className='SingleApp'>
						<img alt='SmartBrain SingleApp' src={SuperCalculator}></img> 
						<div className='SingleApp-Text'> 
							<div onClick={() => onRouteChange('SuperCalculator')} > <span> View  </span> </div>
						</div>
					</div>
					<div className='SingleApp'>
						<img alt='SmartBrain SingleApp' src={WeatherApp}></img> 
						<div className='SingleApp-Text'> 
							<div onClick={() => alert("App will be available soon!")} href='#AppsList'> <span> TBD  </span> </div>
						</div>
					</div>
				</div>
			</div>
			)
}


export default AppsList