import React from 'react';
import './LandingPage.css';



import SideNav from '../../Components/LandingPage/SideNav/SideNav.js';
import Profile from '../../Components/LandingPage/Profile/Profile.js';
import Landing from '../../Components/LandingPage/Landing/Landing.js';
import ContactMe from '../../Components/LandingPage/ContactMe/ContactMe.js';
import Projects from '../../Components/LandingPage/Projects/Projects.js';



class LandingPage extends React.Component {
  constructor(){
      super()
      this.state={
        MainRoute: 'Landing',
        route: 'home',
      }
  }

  componentDidMount(){
  }

  onRouteChange = (route) => {
    this.setState({route : route});
  }
  onMainRouteChange = (MainRoute) => {
    this.props.MainRouteChange(MainRoute);
  }



  render(){
    return (
    <div className="LandingPage">
      
      <SideNav 
        onRouteChange={this.onRouteChange}
        onMainRouteChange={this.onMainRouteChange}
      />
      
      {this.state.route === 'home'
        ? <div>
            <Landing/>
            <Profile/>
            <Projects
              onMainRouteChange={this.onMainRouteChange}
            />
            <ContactMe/>
          </div>
        : (this.state.route === 'about'
            ? <Profile/>
            : (this.state.route === 'contactInfo'
                ? <ContactMe/>
                : (this.state.route === 'projects'
                ? <Projects
                    onMainRouteChange={this.onMainRouteChange}
                  />
                : <div>
                      <Landing/>
                      <Profile/>
                      <Projects
                        onMainRouteChange={this.onMainRouteChange}
                      />
                      <ContactMe/>
                    </div>
                  )    
             ) 
          )
      }
    </div>
  );
    }
  
}

export default LandingPage;
