import React, { useState } from 'react';
import {SiPagekit} from "react-icons/si";
import './WebsitesAbout.css';

/*
Icons Used from React-icons (https://react-icons.github.io/react-icons/) :
1. SiHomeassistant for Home
2. SiPagekit for CV
3. SiProgress for Projects
4. SiWebsiteannie for Websites
5. SiWebsitelearcade for Websites
6. SiWebmoney for Websites
7. SiAzuredataexplorer for Contact
*/
const WebsitesAbout = ({onRouteChange, onMainRouteChange}) => {

	const [AboutActive, setAboutActive] = useState("false");
		const AboutToggle = () => {
			setAboutActive(!AboutActive);
		};

		return(
			<div 
				className={`WebsitesAbout ${AboutActive ? "WebsitesAbout-Hidden" : "WebsitesAbout-NotHidden"}`}
				onClick={AboutToggle}
			>
				<div className={`WebsitesAbout-website  ${AboutActive ? "Hidden-Text" : "NotHidden-Text"}`}>
					<h2> About the Website</h2>
					<p> This website contains the Websites that I developed. The Websites are listed below, feel free to explore them!</p>
				</div>
				<div className={`WebsitesAbout-Button  ${AboutActive ? "NotHidden-Text" : "Hidden-Text"}`}
				>
					<span> About </span>
				</div>
				<div className={`WebsitesAbout-Website  ${AboutActive ? "Hidden-Text" : "NotHidden-Text"}`}>
					<h2> About the Websites</h2>
					<p> Each Website is developed from scratch using React (HTML/CSS/JS). Every Website serves as a component that can be used in other websites. Contact me for Website requests!</p>
				</div>
			</div>
			
	);
}


export default WebsitesAbout