import { Textfit } from "react-textfit";
import "./Screen.css";

const Screen = ({ value, result, pastresult }) => {
  return (
    <div className="screen">
      <Textfit className="screen-line screen-value" mode="single" forceSingleModeWidth={false} max={80}>
        <span>{value}</span>
      </Textfit>
      <Textfit className="screen-line screen-result" mode="single" forceSingleModeWidth={false} max={20}>
        <span>{result}</span>
      </Textfit>
      <Textfit className="screen-line screen-pastresult" mode="single" forceSingleModeWidth={false} max={15}>
        <span className='pastresult'>{pastresult}</span>
      </Textfit>
      
    </div>
  );
};

export default Screen;